.escort-menu {
  list-style: none;
  margin: 0;
  padding: 0;

  > li {

    > .escort-item {
      float: none;
    }
  }
}

.escort-type-menu.escort-container {

  .escort-menu {
    list-style: none;
    margin: 0;
    padding: 0;

    ul {
      list-style: none;
      margin: 0;
      padding: 0;
    }

    li {
      list-style: none;
      list-style-image: none;
    }
  }
}
