// Styles applied to the aside display mode for widgets/escorts.

.escort-aside-content {
  opacity: 0;

  &.escort-active {
    opacity: 1;
  }
}

.escort-region-after {

  .escort-aside-display-shelf {
    transition: width 300ms, padding 300ms, left 300ms, right 300ms, box-shadow 300ms;
  }
}
